/* Menu Default
- - - - - - - - - - - - - - - - - - - - - - */
ul.menu,
ul.submenu {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

ul.menu > li {
  float: left;
  position: relative;
}

ul.menu > li:hover {
  z-index: 990;
}

ul.menu > li > a {
  text-decoration: none;
  color: var(--mainmenu-active-color);
  display: block;
  white-space: nowrap;
  border-radius: 0;
}

/* Menu Wrapper
- - - - - - - - - - - - - - - - - - - - - - */
.menu-wrapper {
  position: relative;
}

.menu-wrapper > .close-mobile {
  display: none;
}

#main.fixed {
  padding-top: 63px;
}

#main.fixed .menu-wrapper {
  width: calc(100% - 72px);
  z-index: 950;
  position: fixed;
  top: 0;
  border-bottom: 3px solid #2DCA73;
}

#main.fixed .menu-wrapper #current {
  padding-top: 10px;
}

