#profile-menu{
  display: none;
  position: absolute;
  right: 16px;
  top: 55px;
  z-index: 1000;
  border: 0.4px solid #D9D9D9;
  box-shadow: 0 9px 40px rgba(0, 0, 0, 0.06);
  border-radius: 2px;
  background-color: white;

  &.visible{
    display: block;
  }

  a{
    display: flex;
    align-items: center;
    padding: 10px 15px;
    width: 100%;
    text-align: left;
    color: #25233A;
    font-size: 14px;

    &:last-child{
      border-top: 1px solid rgba(151, 151, 151, 0.26);
    }

    &:hover{
      background: #FAFBFD;
    }

    svg{
      margin-right: 10px;
    }
  }
}